import React, { Component } from 'react';
import { Tab, ListGroup } from 'react-bootstrap'
import defImage from '../../../../assets/images/item-02.jpg';

class ProductDetailImages extends Component {
    render() {
        const { images } = this.props;
        return (
            <div class="w-size13 p-t-30 respon5">
                <div class="wrap-slick3 flex-sb flex-w">
                    <Tab.Container id="images-group-tabs" defaultActiveKey="#image-link1">
                        <ListGroup className="wrap-slick3-dots">               
                            {this.renderDotImages(images)}
                        </ListGroup>
                        <Tab.Content className="slick3">
                            {this.renderImages(images)}
                        </Tab.Content>
                    </Tab.Container>
                </div>
            </div>
        );
    }

    renderDotImages = (images) => {
        return images.map((image,index) =>{
            console.log(image);
            return (
                <ListGroup.Item action href={"#image-link"+index} className={'image-dot-item'}>
                    <img src={ image.url ? image.url : defImage } />
                    <div class="slick3-dot-overlay"></div>
                </ListGroup.Item>
            )
        });
    }

    renderImages = (images) => {
        return images.map((image,index) =>{
            return (
                <Tab.Pane eventKey={`#image-link${index}`} key={index}>
                    <div class="item-slick3">
                        <div class="wrap-pic-w">
                            <img src={ image.url ? image.url : defImage } alt="IMG-PRODUCT" />
                        </div>
                    </div>
                </Tab.Pane>
            )
        });
    }
}


export default ProductDetailImages;
