import React, { Component } from 'react'
import { connect } from 'react-redux'

import { setProduct } from '../../actions'

class AddToCartButton extends Component {

    handleClickAddToCard = () => {
        const { setProduct,product, totalCount } = this.props;
        const newCartItem = {
            totalCount:totalCount,
            item:product
        }
        setProduct(newCartItem);
    }

  render() {
    return (
        <div class="btn-addcart-product-detail size9 trans-0-4 m-t-10 m-b-10" onClick={this.handleClickAddToCard}>
            <button class="flex-c-m sizefull bg1 bo-rad-23 hov1 s-text1 trans-0-4">
                Add to Cart
            </button>
        </div>
    )
  }
}

const mapStateToProps = (state) => ({
  cart:state.cart
})

const mapDispatchToProps = {
    setProduct
}

export default connect(mapStateToProps, mapDispatchToProps)(AddToCartButton)
