import React, { Component } from 'react'
import { connect } from 'react-redux'
import CartItem from './CartItem';

class CartItems extends Component {

    renderItems = () => {
        const { mobile, cart } = this.props;
        
        return cart.items.map((element, index) => {
            return (
                <CartItem  key={index} mobile={ !mobile ? false : true } {...element}/>
            )
        })
    }

    render() {
        const { mobile, cart } = this.props;
        if(!mobile){
        return (
                <div className="container-table-cart pos-relative">
                    <div className="wrap-table-shopping-cart bgwhite">
                        <table className="table-shopping-cart">
                            <tbody>
                                <tr className="table-head">
                                    <th className="column-1"></th>
                                    <th className="column-2">Product</th>
                                    <th className="column-3">Price</th>
                                    <th className="column-4 p-l-70">Quantity</th>
                                    <th className="column-5">Total</th>
                                </tr>
    
                                {this.renderItems()}
                            </tbody>
                        </table>
                    </div>
                </div>
        );
        }else{
            return (
                   <div className={'container-mobile-cart'}>
                        <div className={'d-flex flex-column w-100'}>
                            {this.renderItems()}
                        </div>
                   </div>
              );
        }
      }
}

const mapStateToProps = (state) => ({
  cart:state.cart
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(CartItems)

