import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import icon from '../../images/icons/icon-header-02.png'
import classnames from 'classnames';
import { connect } from 'react-redux'

class CartToggler extends Component {
    constructor(props){
        super(props);
        this.state={
          active:false
        }
        this.handleCartClick = this.handleCartClick.bind(this);
    }

    handleCartClick(){
        this.setState(state => ({
            active: !state.active,
        }));
    }

    renderItems = (data) => {
        return data.map((element, index) => {
            const item = element.item;
            return (
                <li className="header-cart-item" key={index}>
                    <div className="header-cart-item-img">
                        <img src={item.images[0].url} alt="IMG" />
                    </div>
    
                    <div className="header-cart-item-txt">
                        <Link to={`/product/${item.id}`} className="header-cart-item-name">
                            {item.title}
                        </Link>
    
                        <span className="header-cart-item-info">
                            {element.totalCount} x {item.currency}{item.price}
                        </span>
                    </div>
                </li>
            )
        });
    }
    
    renderItemsDropdown = () => {
        const { cart } = this.props;
        if(!cart.isReady){
            return (
                <div className={classnames('header-cart header-dropdown', this.state.active ? 'show-header-dropdown' : '' )}>
                    Your cart is empty!
                </div>)
        }else{
            return (
                <div className={classnames('header-cart header-dropdown', this.state.active ? 'show-header-dropdown' : '' )}>
                    
                    <ul className="header-cart-wrapitem">
                        {this.renderItems(cart.items)}
                    </ul>
    
                    <div className="header-cart-total">
                        Total: ${cart.items.reduce((prev,next) => prev + next.item.price,0)}
                    </div>
    
                    <div className="header-cart-buttons">
                        <div className="header-cart-wrapbtn">
                            <Link to="/cart" className="flex-c-m size1 bg1 bo-rad-20 hov1 s-text1 trans-0-4">
                            View Cart
                            </Link>
                        </div>
    
                        <div className="header-cart-wrapbtn">
                            <Link to="/cart" className="flex-c-m size1 bg1 bo-rad-20 hov1 s-text1 trans-0-4">
                            Check Out
                            </Link>
                        </div>
                    </div>
                </div>
            )
        }
    }

  render() {
      const { cart } = this.props;
    return (
        <div className="header-wrapicon2">
            <img src={icon} className="header-icon1 js-show-header-dropdown" alt="ICON" onClick={this.handleCartClick.bind(this)}
                aria-expanded={this.state.active} />
            <span className="header-icons-noti">{cart.items.length}</span>
            {this.renderItemsDropdown()}

        </div>
    )
  }
}

const mapStateToProps = (state) => ({
  cart:state.cart
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(CartToggler)
