import React, { Component } from 'react';
import { Row, Col,Card } from 'react-bootstrap';
import image from '../../../../assets/images/item-10.jpg'
import { TotalCounter } from '../../../helpers';

class CartItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            totalCount: 1
        }
    }

    handleChangeTotal = (e) => {
        const max = 10;
        const min = 1;
        if (e.target.getAttribute('data-direction') === 'up') {
            this.setState(state => {
                if (state.totalCount < max) {
                    return {
                        totalCount: state.totalCount + 1
                    }
                }
            });
        } else {
            this.setState(state => {
                if (state.totalCount != min) {
                    return {
                        totalCount: state.totalCount - 1
                    }
                }
            });
        }
    }

  render() {
    const { mobile, item } = this.props;
    if(!mobile){
    return (
        <tr className="table-row">
            <td className="column-1">
                <div className="cart-img-product b-rad-4 o-f-hidden">
                    <img src={item.images[0].url} alt="IMG-PRODUCT"/>
                </div>
            </td>
            <td className="column-2">{item.title}</td>
            <td className="column-3">{item.currency}{item.price}</td>
            <td className="column-4">
                <TotalCounter handleChange={this.handleChangeTotal} totalCount={this.state.totalCount} className={'flex-w bo5 of-hidden w-size17'}/>
            </td>
            <td className="column-5">{item.currency}{item.price}</td>
        </tr>
    );
    }else{
        return (
            <Card>
            <Card.Body>
              <Row>
                <Col xs={4}>
                  <div className="cart-img-product b-rad-4 o-f-hidden">
                    <img src={item.images[0].url} alt="IMG-PRODUCT" />
                  </div>
                </Col>
                <Col xs={8} className={'p-l-0'}>
                  <div style={{'height':'80%'}} className={'d-flex justify-content-end align-items-center'}>
                    <div className={'text-center'}>
                      {item.title}
                    </div>
                    <button type="button" class="close cart-close" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>
                  <div style={{'height':'20%'}}>
                    <Row>
                      <Col sx={6}>
                        <div>
                            <div class="flex-w bo5 of-hidden cart-item-count flex-column flex-sm-row">
                                <button class="btn-num-product-down color1 flex-c-m size7 bg8 eff2">
                                    <i class="fs-12 fa fa-minus" aria-hidden="true"></i>
                                </button>

                                <input class="size7 m-text18 t-center num-product" type="number" name="num-product1" value="1" />

                                <button class="btn-num-product-up color1 flex-c-m size7 bg8 eff2">
                                    <i class="fs-12 fa fa-plus" aria-hidden="true"></i>
                                </button>
                            </div>
                        </div>
                      </Col>
                      <Col sx={6} className={'d-flex justify-content-end'}>
                        {item.currency}{item.price}
                      </Col>
                    </Row>
                  </div>
                </Col>
              </Row>
              </Card.Body>
        </Card>
          );
    }
  }
}

export default CartItem;
