import React, { Component } from 'react'

class TotalCounter extends Component {
  render() {
      const { totalCount,handleChange, className } = this.props;
      console.log(totalCount);
    return (
        <div className={className ? className : 'flex-w bo5 of-hidden m-r-22 m-t-10 m-b-10'}>
            <button class="btn-num-product-down color1 flex-c-m size7 bg8 eff2" data-direction={'down'} onClick={handleChange}>
                <i class="fs-12 fa fa-minus" aria-hidden="true"></i>
            </button>

            <input class="size8 m-text18 t-center num-product" type="number" name="num-product" value={totalCount} />

            <button class="btn-num-product-up color1 flex-c-m size7 bg8 eff2" data-direction={'up'} onClick={handleChange}>
                <i class="fs-12 fa fa-plus" aria-hidden="true"></i>
            </button>
        </div>
    )
  }
}

export default TotalCounter
