import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Container } from 'react-bootstrap';
import classnames from 'classnames';
import { Jumbotron } from '../../page-jumbotron';
import { CartItems, CartTotal } from './components';

class CartPage extends Component {

  cardCheckEmpty(){
    const { isReady } = this.props;
    if(isReady===true){
      return (
        <div>
          <CartItems mobile={false}/>
            <CartItems mobile={true}/>
          <CartTotal/>
        </div>
      );
    }else{
      return "You'r card is empty!"
    }
  }
  render() {
    return (
      <div>
        <Jumbotron title={"Cart Page"}/>
        <Container fluid={true} className={classnames('p-3','pr-md-5','pl-md-5','pt-md-3','pb-md-3')}>
            {this.cardCheckEmpty()}
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  cart:state.cart
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(CartPage)


