import React, { Component } from 'react';
import classnames from 'classnames';

class ContentMenu extends Component {
  render() {
    const { mobile, active=null } = this.props;
    if(!mobile){
        return (
            <div className="wrap_menu">
                <nav className="menu">
                        {this.props.children}
                </nav>
            </div>
        );
    }else{
        return (
            <div class={classnames('wrap-side-menu z-index-max', active ? 'd-flex' : '')}>
                <nav class="side-menu">
                        {this.props.children}
                </nav>
            </div>
        );
    }
  }
}

export default ContentMenu;