import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import defaultimage from '../../../../assets/images/item-02.jpg';

class ProductCard extends Component {
  render() {
      const { id, title, price, currency, images } = this.props;
    return (
        <Link to={`/product/${id}`}>

            <div class="block2">
                <div class="block2-img wrap-pic-w of-hidden pos-relative block2-labelnew">
                    <img src={ images[0].url ? images[0].url : defaultimage} alt="IMG-PRODUCT" />

                    <div class="block2-overlay trans-0-4">
                        <Link to={`/product/${id}`} class="block2-btn-addwishlist hov-pointer trans-0-4">
                            <i class="icon-wishlist icon_heart_alt" aria-hidden="true"></i>
                            <i class="icon-wishlist icon_heart dis-none" aria-hidden="true"></i>
                        </Link>
                    </div>
                </div>

                <div class="block2-txt p-t-20">
                    <div class="block2-name dis-block s-text3 p-b-5">
                        {title}/product/1
                    </div>
                    <span class="block2-price m-text6 p-r-5">
                        {currency}{price}
                    </span>
                </div>
            </div>
            </Link>
    );
  }
}

export default ProductCard;
