import React, { Component } from 'react';
import Select from 'react-select';

class SortingOrder extends Component {
    constructor(props){
        super(props);
        this.options = [
            { value: 1, label: 'Default Sorting' },
            { value: 2, label: 'Popularity' },
            { value: 3, label: 'Price: low to high' },
            { value: 4, label: 'Price: high to low'}
        ];
        this.state={
            selectedOption:this.options[0]
        }
        this.handleChange=this.handleChange.bind(this);
    }
    
    handleChange = (selectedOption) => {
        this.setState({ selectedOption });
    }
  render() {
    return (
        <div class="mb-3">
            <Select value={this.state.selectedOption} onChange={this.handleChange} options={this.options} isSearchable={false} className={"z-index-max"}/>
        </div>
    );
  }
}

export default SortingOrder;
