import React, { Component } from 'react';
import { ContentLogo, ContentMenu } from './index'

class ContanetBar extends Component {
  render() {
    const { mobile, children }=this.props;
    const wrapperClass={
      mobile:'wrap_header_mobile',
      web:'wrap_header'
    };
    return (
        <div className={!mobile ? wrapperClass['web'] : wrapperClass['mobile'] }>
            <ContentLogo mobile={mobile}/>
            {children}
        </div>
    );
  }
}

export default ContanetBar;