import React, { Component } from 'react';
import InputRange from 'react-input-range';

import './css/react-input-range.css';

class PriceFilter extends Component {
    constructor(props){
        super(props);
        this.state={
            minValue:50,
            maxValue:200,
            selectedValue:{
                min:50,
                max:150
            }
        }
    }
    
  render() {
    return (
        <div className="filter-price p-t-22 p-b-50 bo3">
            <div className="m-text15 p-b-17">
                Price
            </div>

            <div>
                <InputRange
                    draggableTrack
                    maxValue={this.state.maxValue}
                    minValue={this.state.minValue}
                    value={this.state.selectedValue}
                    onChange={value => this.setState({ selectedValue:value })}
                    onChangeComplete={value => console.log(value)} />
            </div>

            <div className="flex-sb-m flex-w p-t-16">
                <div className="w-size11">
                    <button className="flex-c-m size4 bg7 bo-rad-15 hov1 s-text14 trans-0-4">
                        Filter
                    </button>
                </div>

                <div className="s-text3 p-t-10 p-b-10">
                    Range: $<span id="value-lower">{this.state.selectedValue.min}</span> - $<span id="value-upper">{this.state.selectedValue.max}</span>
                </div>
            </div>
        </div>
    );
  }
}

export default PriceFilter;
