import React, { Component } from 'react';
import classnames from 'classnames';

class MobileMenuToggler extends Component {
  render() {
    const { clickHandler, active } = this.props;
    return (
        <div className={classnames('btn-show-menu-mobile hamburger hamburger--squeeze', active ? 'is-active' : '')} onClick={clickHandler.bind(this)}>
            <span className="hamburger-box">
                <span className="hamburger-inner"></span>
            </span>
        </div>
    );
  }
}

export default MobileMenuToggler;