import React,{Component} from 'react';
import _ from 'lodash';
import { Accordion, Icon } from 'semantic-ui-react'

class ProductAcordions extends Component {
    
  constructor(props) {
    super(props);
    this.state = { 
        activeIndex: 0,
        items:[]
    };

    const panels=_.times(3, i => ({
        key: `panel-${i}`,
        title: `Title-${i}`,
        content: `Content-${i}`,
      }))
      this.setState({items:panels})
  }

  handleClick = (e) => {
    const index = e.target.getAttribute('data-index');
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  renderlist(data,activeIndex){
    return data.map((element,index) => {
      return (
          <div key={index}>
            <Accordion.Title active={parseInt(activeIndex)===parseInt(index)} data-index={index} onClick={this.handleClick}>
              <Icon name='dropdown' />
              {element.header}
            </Accordion.Title>
            <Accordion.Content active={parseInt(activeIndex)===parseInt(index)}>
              <p>
                {element.text}
              </p>
            </Accordion.Content>
          </div>
      )
    })
  }

  render(){
    const { activeIndex } = this.state
    const { data } = this.props; 
    return (
      <Accordion>
        {this.renderlist(data,activeIndex)}
      </Accordion>
    )
  }
};

export default ProductAcordions;