import React, { Component } from 'react';
import classnames from 'classnames';

class TopBarSocialIcons extends Component {
  render() {
    const { mobile }=this.props;
    
    return (
        <div className={classnames(!mobile ? 'topbar-social' : 'topbar-social-mobile')}>
            <a href="https://google.com" className="topbar-social-item fa fa-facebook"></a>
            <a href="https://google.com" className="topbar-social-item fa fa-instagram"></a>
            <a href="https://google.com" className="topbar-social-item fa fa-pinterest-p"></a>
            <a href="https://google.com" className="topbar-social-item fa fa-snapchat-ghost"></a>
            <a href="https://google.com" className="topbar-social-item fa fa-youtube-play"></a>
        </div>
    );
  }
}

export default TopBarSocialIcons;