import React, { Component } from 'react';

class ColorFilter extends Component {
    constructor(props){
        super(props);
        this.state={
            colorsSelected:[

            ]
        }
        this.handeSelect = this.handeSelect.bind(this);
    }

    handeSelect= (e, data) => {
        const id=e.target.getAttribute('data-id');
        if(this.state.colorsSelected.indexOf(id)>=0){
            this.setState(state => {
                const colorsSelected = state.colorsSelected.filter(item => item !== id);

                return {
                    colorsSelected,
                };
            });
        }else{
            this.setState(state => ({
                colorsSelected: [...state.colorsSelected, id]
            }));

        }
    }
    
  render() {
    return (
            <div class="filter-color p-t-22 p-b-50 bo3">
                <div class="m-text15 p-b-12">
                    Color
                </div>

                <ul class="flex-w">
                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="1" id="color-filter1" type="checkbox" name="color-filter1" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter1" htmlFor="color-filter1"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="2" id="color-filter2" type="checkbox" name="color-filter2" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter2" htmlFor="color-filter2"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="3" id="color-filter3" type="checkbox" name="color-filter3" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter3" htmlFor="color-filter3"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="4" id="color-filter4" type="checkbox" name="color-filter4" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter4" htmlFor="color-filter4"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="5" id="color-filter5" type="checkbox" name="color-filter5" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter5" htmlFor="color-filter5"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="6" id="color-filter6" type="checkbox" name="color-filter6" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter6" htmlFor="color-filter6"></label>
                    </li>

                    <li class="m-r-10">
                        <input class="checkbox-color-filter" data-id="7" id="color-filter7" type="checkbox" name="color-filter7" onChange={this.handeSelect.bind(this)}/>
                        <label class="color-filter color-filter7" htmlFor="color-filter7"></label>
                    </li>
                </ul>
            </div>
    );
  }
}

export default ColorFilter;
