import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import classnames from 'classnames';
import { Container, Row, Col } from 'react-bootstrap';
import { Jumbotron } from '../../page-jumbotron';
import ProductCard from './components/ProductCard';
import { FilterComponent, SortingOrder } from '../../filters';

import { fetchProducts } from '../../../actions';

class ProductsPage extends Component {
  static propTypes = {
    prop: PropTypes
  }

  componentDidMount(){
    this.props.fetchProducts();
  }

  render() {
    const { products } = this.props;
    console.log(products);
    return (
      <div>
        <Jumbotron title={"Product Page"}/>
        <Container fluid={true} className={classnames('p-3','pr-md-5','pl-md-5','pt-md-3','pb-md-3')}>
          <Row>
            <Col sm={6} md={4} lg={3} className={classnames('p-b-50')}>
              <div className={classnames('leftbar p-r-20 p-r-0-sm')}>
                <FilterComponent/>
              </div>
            </Col>
            <Col sm={6} md={8} lg={9} className={classnames('p-b-50')}>
              <Row>
                <Col sm={12} md={6} lg={4}>
                  <SortingOrder/>
                </Col>
              </Row>
              <Row>
              {!products.isReady
                ? 'Loading...'
                : products.items.map((product, i) => 
                  <Col sm={12} md={6} lg={4} className={classnames('p-b-50')} key={i}>
                    <ProductCard {...product}/>
                  </Col>
                )}
              </Row>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  products:state.products
})

const mapDispatchToProps = {
  fetchProducts
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductsPage)

