import React, { Component } from 'react';
import { TopBar } from './components/topbar';
import { ContanetBar, ContentMenu, ContentMenuList, MobileMenuToggler, UserAccount } from './components/content';
import { CartToggler } from './components/cart';
import { withRouter } from 'react-router-dom';

class Header extends Component {
  constructor(props){
    super(props);
    this.state={
      menuActive:false,
      cartActive:false
    }

    this.unlisten=null;

    this.handleMenuTogglerClick = this.handleMenuTogglerClick.bind(this);
    this.handleCartClick = this.handleCartClick.bind(this);
  }

  componentWillMount() {
    this.unlisten = this.props.history.listen((location, action) => {
      this.setState({menuActive:false});
    });
  }

  componentWillUnmount() {
      this.unlisten();
  }

  handleMenuTogglerClick(){
    this.setState(state => ({
      menuActive: !state.menuActive,
      cartActive: false
    }));
  }

  handleCartClick(){
    this.setState(state => ({
	  cartActive: !state.cartActive,
	  menuActive: false
    }));
  }

  render() {
    return (
      <header className="header1">
        <div className="container-menu-header">
            <TopBar mobile={false}/>
            <ContanetBar mobile={false}>
              <ContentMenu mobile={false}>
                <ContentMenuList mobile={false}/>
              </ContentMenu>
                <div class="header-icons">
                  <UserAccount/>

                  <span class="linedivide1"></span>

                  <CartToggler clickHandler={this.handleCartClick} active={this.state.cartActive}/>
                </div>
            </ContanetBar>
        </div>
        <ContanetBar mobile={true}>
          <div className="btn-show-menu">
            <div className="header-icons-mobile">
              <UserAccount/>

              <span className="linedivide2"></span>

              <CartToggler clickHandler={this.handleCartClick} active={this.state.cartActive}/>
            </div>
            <MobileMenuToggler clickHandler={this.handleMenuTogglerClick} active={this.state.menuActive}/>
          </div>
        </ContanetBar>
        <ContentMenu mobile={true} active={this.state.menuActive}>
          <TopBar mobile={true}/>
          <ContentMenuList mobile={true}/>
        </ContentMenu>
      </header>
    );
  }
}

export default withRouter(Header);