import React, { Component } from 'react';
import { connect } from 'react-redux'
import { Container, Col, Row } from 'react-bootstrap';
import _ from 'lodash';
import classnames from 'classnames';
import { Select } from 'semantic-ui-react'
import { ProductImages, ProductAcordions } from './components';

import { fetchProduct } from '../../../actions'
import { TotalCounter, AddToCartButton } from '../../helpers';

class ProductPage extends Component {
  constructor(props){
    super(props);
    this.state = {
        totalCount:1
    }
  }

  componentDidMount(){
    this.props.fetchProduct(this.props.match.params.id);
  }
  
  renderAttributes(data){
    return data.map((element,index) => {
        return <Select key={index} placeholder='Size' options={element.values} />
    })
  }

  handleChangeTotal = (e) => {
      const max=10;
      const min=1;
    if(e.target.getAttribute('data-direction')==='up'){
        this.setState(state => {
            if(state.totalCount<max){
                return {totalCount:state.totalCount+1}
            }
        });
    }else{
        this.setState(state => {
            if(state.totalCount!=min){
                return {totalCount:state.totalCount-1}
            }
        });
    }
  }

  render() {
    const { product } = this.props;

    if(!product.isReady){
        return (
            'Loading...'
        )
    }else{
        return (
            <div>
              <Container fluid={true} className={classnames('p-3','pr-md-5','pl-md-5','pt-md-3','pb-md-3')}>
                <div class="flex-w flex-sb">
                  <ProductImages images={product.item.images}/>
      
                  <div class="w-size14 p-t-30 respon5">
                  <div>
                      <h4 class="product-detail-name m-text16 p-b-13">
                        {product.item.title}
                      </h4>
      
                      <span class="m-text17">
                        {product.item.currency}{product.item.price}
                      </span>
      
                      <p class="s-text8 p-t-10">
                        {product.item.description}
                      </p>
                    </div>
                    <div class="p-t-33 p-b-60">
                      <Row>
                        <Col xs={6} className={'d-flex flex-column justify-content-around'} style={{'height':'100px'}}>
                            {this.renderAttributes(product.item.attributes)}
                        </Col>
                      </Row>
      
                      <div class="flex-r-m flex-w p-t-10">
                        <div class="w-size16 flex-m flex-w">
                            <TotalCounter handleChange={this.handleChangeTotal} totalCount={this.state.totalCount}/>
      
                            <AddToCartButton product={product.item} totalCount={this.state.totalCount}/>
                        </div>
                      </div>
                    </div>
      
                    <div class="p-b-45">
                      <span class="s-text8 m-r-35">SKU: {product.item.sku}</span>
                      <span class="s-text8">Categories: {product.item.category.name}</span>
                    </div>
                  <ProductAcordions data={product.item.additional}/>              
                  </div>
                </div>
              </Container>
            </div>
          );
    }
  }
}

const mapStateToProps = (state) => (console.log(state),{
  product:state.product
})

const mapDispatchToProps = {
    fetchProduct
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductPage)

